<template>
  <div>
    <form autocomplete="off" @submit.prevent="submit">
      <h3>Email</h3>
      <input type="email" placeholder="test@gmail.com" autocomplete="email" v-model="email" :class="{'error': hasError(0, email) }">
      <p class="error" v-if="error">{{ error }}</p>
      <p  v-if="msg">{{ msg }}</p>
      <button type="submit">Passwort zur端cksetzen</button>
    </form>
  </div>
</template>
<script>
  import validateInputs from '@/mixins/validateInputs'
  export default {
    data() {
      return {
        msg: null,
        error: null,
        email: ''
      }
    },
    methods: {
      submit() {
        const _this = this;
        const hasError = this.validateInputs([this.email]);
      
        if (hasError) {
          this.error = 'Bitte f端lle alle Felder aus';
        } else {
          this.$store.dispatch('updateIsLoading', true);
          this.$firebase.auth().sendPasswordResetEmail(_this.email).then(() => {
            _this.msg = 'Wir haben dir eine E-mail geschickt, mit dem du das Passwort zur端cksetzen kannst.';
          }).catch(function(error) {
            console.log(error);
          }).finally(() => {
            _this.$store.dispatch('updateIsLoading', false);
          });
        }

      }
    },
    mixins: [validateInputs]
  }
</script>

